import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "../formTicket.scss";

const LoginForm = ({ onSubmit }) => {
  const [validated, setValidated] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  } as IFormLogin);

  const handleChange = (event) => {
    const { name, value, type, checked } = event.target;
    setFormData({
      ...formData,
      [name]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent page reload
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    } else {
      onSubmit(formData);
    }
    setValidated(true);
  };

  return (
    <div className="containterForm">
      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <h3 className="titleForm">Login</h3>
        </Col>
      </Row>

      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Row className="mb-3">
          <Form.Group as={Col} xs="12" sm="12" controlId="formBasicEmail">
            <Form.Label className="label">Email</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="email"
              placeholder="Email"
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Row className="mb-3">
          <Form.Group as={Col} xs="12" sm="12" controlId="formBasicPassword">
            <Form.Label className="label">Contraseña</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="password"
              placeholder="Contraseña"
              name="password"
              value={formData.password}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
        </Row>

        <Form.Group className="sm-3 rowButton">
          <Button
            className="buttonSend"
            variant="outline-warning"
            type="submit"
          >
            Login
          </Button>
        </Form.Group>
      </Form>
    </div>
  );
};

export default LoginForm;

export interface IFormLogin {
  email: string;
  password: string;


}
