import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "./inputSearch.scss";
const InputSearch = ({ onSubmit }) => {
  const [validated, setValidated] = useState(false);
  const [formData, setFormData] = useState({
    code: "",
  });

  const handleChange = (event) => {
    const { name, value, type, checked } = event.target;
    setFormData({
      ...formData,
      [name]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent page reload
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    } else {
      onSubmit("THERAVE"+formData.code);
    }
    setValidated(true);
  };

  return (
    <div className="containterForm">
      <Row>
        <Col xs={12} >
          <h3 className="titleFormSearch">Buscar entrada</h3>
        </Col>
      </Row>

      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Row className="mb-3">
          <Form.Group as={Col} xs="12" sm="12" controlId="validationCustom01">
            <Form.Label className="label">Código</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Código"
              name="code"
              value={formData.code}
              onChange={(e)=>handleChange(e)}
            />
          </Form.Group>
        </Row>
        <Form.Group as={Col} xs="12" sm="12" className="rowButton">
          <Row>
            <Button
              className="buttonSend"
              variant="outline-warning"
              type="submit"
            >
              Buscar
            </Button>
          </Row>
        </Form.Group>
      </Form>
    </div>
  );
};

export default InputSearch;
