import { setItemStorage } from "./sessionStorage.service.ts";

export function getStaffData() {
    let detail = window.sessionStorage[SessionStorageItems.STAFF_DATA];
    return detail ? JSON.parse(detail) : null;
}

export function setStaffData(data: any) {
    setItemStorage(SessionStorageItems.STAFF_DATA, data);
}

export function initStaffData() {
    let detail = window.sessionStorage[SessionStorageItems.STAFF_DATA];
    return detail ? JSON.parse(detail) : null;
}


export function getScannedQRData() {
    let detail = window.sessionStorage[SessionStorageItems.SCANNER_DATA];
    return detail ? detail : null;
}

export function setScannedQRData(data: any) {
    setItemStorage(SessionStorageItems.SCANNER_DATA, data);
}

export enum SessionStorageItems {
    STAFF_DATA = "rave-staff-data",
    SCANNER_DATA="rave-scanner-data"




}