import React from "react";
import {
  HashRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom"; // 👈 Aquí el cambio
import "./App.scss";
import { Home } from "./pages/public/home/home.tsx";
import { DownloadTicket } from "./pages/public/downloadTicket/downloadTicket.tsx";
import { Scanner } from "./pages/private/scanner/scanner.tsx";
import { ShowTicket } from "./pages/private/showTicket/showTicket.tsx";
import { Login } from "./pages/private/login/login.tsx";
import { PrivateHome } from "./pages/private/privateHome/privateHome.tsx";

import { Confirmed } from "./pages/public/confirmed/confirmed.tsx";
import { getStaffData } from "./util/storage/user.session.service.ts";
import { ShowTicketManual } from "./pages/private/showTicketManual/showTicketManual.tsx";
import { ShowAllTickets } from "./pages/private/showAllTickets/showAllTickets.tsx";


function Pages() {
  const logged = getStaffData();


  return (
    <Routes>
        {
          <>
            <Route path="/" element={<Home />} />
            <Route path="/download-ticket" element={<DownloadTicket />} />
            <Route path="/login" element={<Login />} />
            <Route path="/confirmed" element={<Confirmed />} />
          </>
        }
        {/* Private routes */}
        {logged ? (
          /* CUANDO ESTAMOS LOGADOS */
          <>
            {
              <>
                <Route path="/privateHome" element={<PrivateHome />} />
                <Route path="/scanner-qr" element={<Scanner />} />
                <Route path="/showTicket" element={<ShowTicket />} />
                <Route path="/showTicketManual" element={<ShowTicketManual />} />
                <Route path="/showAllTickets" element={<ShowAllTickets />} />

                
              </>
            }
          </>
        ) : (
          /* CUANDO NO ESTAMOS LOGADOS, NOS SALIMOS DE LAS RUTAS PRIVADAS Y NAVEGAMOS A LA HOME PÚBLICA */
          <Route path="*" element={!logged && <Navigate replace to="/" />} />
        )}

    </Routes>
  );
}

function App() {
  return (
    <Router>
      {" "}
      {/* 👈 Ya no necesita basename */}
      <Pages />
    </Router>
  );
}

export default App;
