import React from "react";

import "./header.scss";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
export const Header = () => {
  const images = require("../../media/images/titulo.png")


  return (
    <div className="containerHeader">
      <Row>
        <Col sm={{ span: 12, offset: 12 }}>
        <img
        className="imageLogo"
        src={images}
        style={{ opacity: 1 }}
      />
        </Col>
      </Row>
    </div>
  );
};
