import { IFormLogin } from "../../../components/form/loginForm/loginForm";
import {
  collection,
  db,
  getDocs,
  query,
  where,
} from "../../../service/firebaseConfig";

export async function loginService(data: IFormLogin) {
  try {
    // Realizar la consulta para buscar el documento con el atributo 'ID' igual al valor de ID_TICKET
    const qEmail = query(
      collection(db, "Camareros"),
      where("email", "==", data.email)
    );
    const qPassword = query(
      collection(db, "Camareros"),
      where("password", "==", data.password)
    );

    // Ejecutar la consulta
    const queryEmail = await getDocs(qEmail);
    const queryPassword = await getDocs(qPassword);

    if (queryEmail.empty || queryPassword.empty) {
      console.log("No se encontró el  usuario");
      return; // Salir si no se encuentra el documento
    } else {
        let userInfo = null as any
        queryPassword.forEach(doc => {
            userInfo = doc.data()
        });

      // Actualizar los campos del documento

      return userInfo;
    }
  } catch (error) {
    return false;
  }
}
