import { useEffect, useRef, useState } from "react";
import React from "react";
import QrScanner from "qr-scanner";
import { useNavigate } from "react-router-dom";

const QRScanner = ({ onScan }) => {
  const videoRef = useRef(null as any);
  const [error, setError] = useState(null as any);
  const scannerRef = useRef(null as any);
  const navigate = useNavigate();
  const hasScannedRef = useRef(false); // Para prevenir múltiples escaneos

  useEffect(() => {
    const startScanner = async () => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: {
            facingMode: "environment",
            width: { ideal: 1280 },
            height: { ideal: 720 },
          },
        });

        const video = videoRef.current;
        if (video) {
          video.srcObject = stream;

          video.onloadedmetadata = async () => {
            try {
              await video.play();

              setTimeout(() => {
                if (!video) return;

                const scanner = new QrScanner(
                  video,
                  (result) => {
                    if (!hasScannedRef.current) {
                      hasScannedRef.current = true;
                      console.log("Resultado detallado:", result);
                      onScan(result);
                      scanner.stop();
                    }
                  },
                  {
                    highlightScanRegion: true,
                    highlightCodeOutline: true,
                    returnDetailedScanResult: true,
                  }
                );

                scanner.setInversionMode('both');
                scannerRef.current = scanner;
                scanner.start();
              }, 500); // esperar a que el video tenga buena imagen

            } catch (playErr) {
              console.error("Error al reproducir video:", playErr);
              setError("No se pudo reproducir la cámara.");
            }
          };
        }
      } catch (err) {
        console.error("Error accediendo a la cámara:", err);
        setError("Error al acceder a la cámara");
      }
    };

    startScanner();

    // Intervalo de escaneo manual
    const interval = setInterval(() => {
      if (videoRef.current && !hasScannedRef.current) {
        console.log("Intentando escanear manualmente...");
        QrScanner.scanImage(videoRef.current)
          .then(result => {
            if (result) {
              console.log("Resultado manual detectado:", result);
              if (!hasScannedRef.current) {
                hasScannedRef.current = true;
                onScan(result);
              }
            }
          })
          .catch(err => console.log("No se pudo detectar QR manualmente:", err));
      }
    }, 1000); // Intenta escanear cada segundo

    return () => {
      stopScanner();
      clearInterval(interval); // Limpiar el intervalo cuando el componente se desmonta
    };
  }, [onScan]);

  const stopScanner = () => {
    if (scannerRef.current) {
      scannerRef.current.stop();
      scannerRef.current = null;
    }

    hasScannedRef.current = false;

    if (videoRef.current) {
      const videoEl = videoRef.current;
      const stream = videoEl.srcObject;

      if (stream) {
        const tracks = stream.getTracks();
        tracks.forEach((track) => track.stop());
      }

      videoEl.srcObject = null;
    }
  };

  const handleCancel = () => {
    stopScanner();
    navigate("/privateHome");
  };

  return (
    <div style={{ position: "relative", width: "100%", height: "100vh", overflow: "hidden" }}>
      {error && <p style={{ color: "red" }}>{error}</p>}

      <video
        ref={videoRef}
        style={{ width: "100%", height: "100%", objectFit: "cover" }}
        muted
        playsInline
      />

      {/* Overlay */}
      <div style={{
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        background: "rgba(0, 0, 0, 0.5)",
        pointerEvents: "none",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
      }}>
        <div style={{
          width: "80vw",
          maxWidth: "400px",
          aspectRatio: 1,
          border: "2px solid lime",
          boxShadow: "0 0 15px lime",
          position: "relative",
          background: "transparent",
        }}>
          <div style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "2px",
            background: "lime",
            animation: "scan 2s linear infinite"
          }} />
        </div>
      </div>

      {/* Cancel Button */}
      <div style={{
        position: "absolute",
        bottom: 30,
        width: "100%",
        display: "flex",
        justifyContent: "center",
        pointerEvents: "auto",
      }}>
        <button
          onClick={handleCancel}
          style={{
            backgroundColor: "rgba(0,0,0,0.6)",
            color: "white",
            border: "none",
            padding: "10px 20px",
            borderRadius: "30px",
            fontSize: "16px",
            display: "flex",
            alignItems: "center",
            gap: "8px",
            cursor: "pointer",
          }}
        >
          <span style={{ fontSize: "20px" }}>⬅️</span> Cancelar
        </button>
      </div>

      {/* Animación */}
      <style>
        {`
          @keyframes scan {
            0% { top: 0; }
            100% { top: 100%; }
          }
        `}
      </style>
    </div>
  );
};

export default QRScanner;
