import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { SessionStorageItems } from "../../../util/storage/user.session.service.ts";
import { removeItemStorage } from "../../../util/storage/sessionStorage.service.ts";

export const PrivateHome = () => {
  const navigate = useNavigate();
  const onNavigateClick = (event: string) => {
    if (event === "logout") {
      removeItemStorage(SessionStorageItems.STAFF_DATA)
      navigate('/');
    } else {
      navigate(event);
    }
  };

  return (
    <div className="containterForm">
      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <h3 className="titleForm">STAFF</h3>
        </Col>
      </Row>

      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-warning"
            onClick={() => onNavigateClick("/scanner-qr")}
          >
            ESCANEAR QR
          </Button>
        </Col>
      </Row>

      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-warning"
            onClick={() => onNavigateClick("/showTicketManual")}
          >
            BUSQUEDA MANUAL QR
          </Button>
        </Col>
      </Row>
      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-warning"
            onClick={() => onNavigateClick("/showAllTickets")}
          >
            MÉTRICAS
          </Button>
        </Col>
      </Row>

      <Row className="mb-3">
        <Col as={Col} xs="12" sm="12">
          <Button
            className="buttonSend"
            variant="outline-light"
            onClick={() => onNavigateClick("logout")}
          >
            CERRAR SESIÓN
          </Button>
        </Col>
      </Row>
    </div>
  );
};
