import React, { useEffect, useState } from "react";
import { getAllTicket } from "./showAllTickets.service.tsx";
import { Loading } from "../../../components/loading/loading.tsx";
import { Button, Row, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
export const ShowAllTickets = () => {
    const navigate = useNavigate();
    const [showLoading, setShowLoading] = useState(false);
    const [dataUsers, setDataUsers] = useState(null as any);
    useEffect(() => {
        const fetchData = async () => {
            setShowLoading(true);

            const result = await getAllTicket();
            setDataUsers(result);
            setShowLoading(false);

        };

        fetchData();
    }, []);



    return (
        <>
            <Loading loading={showLoading} />
            <div className="container">
                {dataUsers && (
                    <Table striped bordered hover responsive>
                        <thead>
                            <tr>
                                <th>Nombre</th>
                                <th>Teléfono</th>
                                <th>VIP</th>
                                <th>Asistencia</th>
                            </tr>
                        </thead>
                        <tbody>
                            {dataUsers.map((ticket, index) => (
                                <tr key={index}>
                                    <td>{ticket.name}</td>
                                    <td>{ticket.phone}</td>
                                    <td>{ticket.vip ? "Sí" : "No"}</td>
                                    <td>{ticket.assistance ? "Sí" : "No"}</td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                )}

                <Row>
                    <Button onClick={() => navigate('/privateHome')} className="buttonBack" variant="outline-info" >
                        Volver
                    </Button>
                </Row>


            </div>
        </>
    );
};
