import { IForm } from "../../../components/form/formEditTicket";
import { collection, db, doc, getDocs, query, updateDoc, where } from "../../../service/firebaseConfig";
export async function getTicket(data: any) {
    try {
        // Crear una consulta a Firestore usando el idQR
        const q = query(
            collection(db, 'Entradas'),  // Reemplaza 'Entradas' con el nombre correcto de la colección
            where('idQR', '==', data)  // Filtrar por el campo idQR
        );

        const querySnapshot = await getDocs(q);

        if (querySnapshot.empty) {
            console.log("No se encontraron entradas con ese idQR.");
        } else {
            let userInfo = null as any
            querySnapshot.forEach(doc => {
                userInfo = doc.data()
            });
            return userInfo
        }
    } catch (error) {
        console.error("Error al obtener entradas:", error);
    }

    // Si lo deseas, puedes cambiar a otra pantalla después de la consulta
    // showScreen('showTicketScreen');
}


export async function editTicketService(data: IForm) {
     try {
             // Realizar la consulta para buscar el documento con el atributo 'ID' igual al valor de ID_TICKET
             const q = query(collection(db, "Entradas"), where("ID", "==", data.ID));
 
             // Ejecutar la consulta
             const querySnapshot = await getDocs(q);
 
             if (querySnapshot.empty) {
                 console.log("No se encontró el documento con ID:", data.ID);
                 return;  // Salir si no se encuentra el documento
             }
 
             // Si encontramos el documento, obtenemos el primer documento de la consulta
             const docSnap = querySnapshot.docs[0];  // Solo hay un documento porque 'ID' es único
 
             // Obtener la referencia del documento
             const docRef = doc(db, "Entradas", docSnap.id);
             const currentDate = new Date();
             // Actualizar los campos del documento
             await updateDoc(docRef, {
                 giftsConsumed: data.giftsConsumed,
                 drinkConsumed: data.drinkConsumed,
                 drinkConsumed2: data.drinkConsumed2,
                 assistance:data.assistance,
                 date: currentDate.toString()
             });
             return true
         } catch (error) {
            return false

         }
}



