import React, { useEffect, useState } from "react";
import LoginForm from "../../../components/form/loginForm/loginForm.tsx";
import { loginService } from "./login.service.tsx";

import { useNavigate } from "react-router-dom";
import { getStaffData, setStaffData } from "../../../util/storage/user.session.service.ts";
import { Loading } from "../../../components/loading/loading.tsx";



export const Login = () => {
  const logged = getStaffData();
  const navigate = useNavigate();
  const [showLoading, setShowLoading] = useState(false);

    useEffect(() => {
      if(logged){
        navigate('/privateHome')
      }
    }, [logged]);


  const loginIn = (data: any) => {
    setShowLoading(true);
    loginService(data)
      .then((result) => {
        if(result===undefined){
            //Mostrar pantalla de error
            setShowLoading(false)
        }else{
            setStaffData(result.ID)
            setShowLoading(false)
            window.location.reload();
          
        }
      })
      .catch((error) => {
        setShowLoading(false)
        console.error("Error iniciar sesión:", error);
      });
  };

  return (
    <>
  
      <Loading loading={showLoading} />
      <div className="container">
        <div className="section2 d-flex justify-content-center align-items-center position-relative">
          <LoginForm
             onSubmit={(e) => loginIn(e)}
          />
        </div>

      </div>
    </>
  );
};
