import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "./formTicket.scss";
import { useNavigate } from "react-router-dom";

const FormEditTicket = ({
    onSubmit,
    initialData,
    onGetPDF, // <- esta sigue igual aquí
}: {
    onSubmit: (data: IForm) => void;
    initialData: IForm;
    onGetPDF?: (data: IForm) => void; // <- marcar como opcional aquí
}) => {
    const navigate = useNavigate();
    const [validated, setValidated] = useState(false);
    const [formData, setFormData] = useState({} as IForm);

    useEffect(() => {
        setFormData({
            ID: initialData?.ID,
            idQR: initialData?.idQR,
            name: initialData?.name,
            phone: initialData?.phone,
            surname1: initialData?.surname1,
            vip: initialData?.vip,
            assistance: initialData?.assistance || false,
            giftsConsumed: initialData?.giftsConsumed || false,
            drinkConsumed: initialData?.drinkConsumed || false,
            drinkConsumed2: initialData?.drinkConsumed2 || false,
        });
    }, [initialData]);

    const handleChange = (event) => {
        const { name, value, type, checked } = event.target;
        setFormData({
            ...formData,
            [name]: type === "checkbox" ? checked : value,
        });
    };

    const handleSubmit = (event) => {
        event.preventDefault(); // Prevent page reload
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.stopPropagation();
        } else {
            onSubmit(formData);
        }
        setValidated(true);
    };

    return (
        <div className="containterForm">
            <Row>
                <Col xs={12} className="colTitle">
                    <h3 className="titleForm">Información entrada</h3>
                </Col>
            </Row>

            <Form noValidate validated={validated} onSubmit={handleSubmit}>
                <Row className="mb-3">
                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom01">
                        <Form.Label className="label">Nombre</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="text"
                            placeholder="Nombre"
                            name="userName"
                            value={formData?.name}
                            disabled={true}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom02">
                        <Form.Label className="label">Apellidos</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="text"
                            placeholder="Apellidos"
                            name="userSurname"
                            value={formData?.surname1}
                            disabled={true}
                        />
                    </Form.Group>

                    <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom03" className="phone">
                        <Form.Label className="label">Teléfono</Form.Label>
                        <Form.Control
                            className={"campo"}
                            required
                            type="tel"
                            placeholder="Teléfono"
                            name="phone"
                            value={formData?.phone}
                            disabled={true}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs="6" sm="4" className="info2">
                        <Form.Check
                            onChange={handleChange}
                            label="Asistencia"
                            name="assistance"
                            checked={formData?.assistance}
                            disabled={initialData?.assistance }
                        />
                    </Form.Group>
                </Row>

                {formData.vip && (
                    <Row>
                        <Form.Group as={Col} xs="6" sm="4" className="info2">
                            <Form.Check
                                onChange={handleChange}
                                label="Regalos"
                                name="giftsConsumed"
                                checked={formData?.giftsConsumed}
                                disabled={initialData?.giftsConsumed}
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs="6" sm="4" className="info2">
                            <Form.Check
                                onChange={handleChange}
                                label="Bebida 1"
                                name="drinkConsumed"
                                checked={formData?.drinkConsumed}
                                disabled={initialData?.drinkConsumed}
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs="6" sm="4" className="info2">
                            <Form.Check
                                onChange={handleChange}
                                label="Bebida 2"
                                name="drinkConsumed2"
                                checked={formData?.drinkConsumed2}
                                disabled={initialData?.drinkConsumed2}
                            />
                        </Form.Group>
                    </Row>
                )}

                <Form.Group className="sm-3 info">
                    <Form.Check
                        label="VIP"
                        name="vip"
                        checked={formData?.vip}
                        disabled={true}
                    />
                </Form.Group>

        
                    <Form.Group  as={Col} xs="6" sm="4" className="rowButton">
                    <Row>
                        <Button onClick={()=>navigate('/privateHome')} className="buttonBack" variant="outline-info" >
                                    Volver
                        </Button>
                        <Button className="buttonSend" variant="outline-warning" type="submit">
                                    Editar
                        </Button>
                        <Button className="buttonPdf" variant="outline-danger" onClick={()=>onGetPDF?.(formData) as any}>
                                    Descargar PDF
                        </Button>
                        </Row>
                    </Form.Group>
               
            </Form>
        </div>
    );
};

export default FormEditTicket;

export interface IForm {
    ID: string;
    idQR: string;
    name: string;
    phone: string;
    surname1: string;
    vip: boolean;
    assistance: boolean;
    giftsConsumed: boolean;
    drinkConsumed: boolean;
    drinkConsumed2: boolean;
}
