import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "./formTicket.scss";
import { Alerts } from "../alerts/alerts.tsx";

const FormTicket = ({ onSubmit, menssageAlert, showAlert }) => {
  const [validated, setValidated] = useState(false);
  const [formData, setFormData] = useState({
    userName: "",
    userSurname: "",
    phone: "",
    aceptaNormas: false,
  });

  const handleChange = (event) => {
    const { name, value, type, checked } = event.target;
  
    // Prepare the value, handling specific cases
    let newValue;
    if (name === "phone") {
      // Remove all non-numeric characters
      newValue = value.replace(/\D/g, '');
    } else {
      newValue = type === "checkbox" ? checked : value;
    }
  
    setFormData({
      ...formData,
      [name]: newValue,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent page reload
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    } else {
      onSubmit(formData);
    }
    setValidated(true);
  };

  return (
    <div className="containterForm">
      <Row>
        <Col xs={12} className="colTitle">
          <h3 className="titleForm">Reservar entrada</h3>
        </Col>
      </Row>

      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Row className="mb-3">
          <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom01">
            <Form.Label className="label">Nombre</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Nombre"
              name="userName"
              value={formData.userName}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom02">
            <Form.Label className="label">Apellidos</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Apellidos"
              name="userSurname"
              value={formData.userSurname}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>

          <Form.Group
            as={Col}
            xs="6"
            sm="4"
            controlId="validationCustom03"
            className="phone"
          >
            <Form.Label className="label">Teléfono</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Teléfono"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
        </Row>

        <Form.Group className="sm-3 info">
          <Form.Check
            required
            label="Acepto las normas de convivencia"
            feedback="Campo requerido"
            feedbackType="invalid"
            name="aceptaNormas"
            checked={formData.aceptaNormas}
            onChange={handleChange}
          />
        </Form.Group>
        <Form.Group className="sm-3 rowButton">
          <Button
            className="buttonSend"
            variant="outline-warning"
            type="submit"
          >
            Reservar
          </Button>
        </Form.Group>
        {showAlert && (
          <Form.Group>
            <Alerts message={menssageAlert} />
          </Form.Group>
        )}
      </Form>
    </div>
  );
};

export default FormTicket;
