import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./countdowTimer.scss";

const CountdownTimer = ({ targetDate }) => {
  const calculateTimeLeft = () => {
    const difference = +new Date(targetDate) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft() as ICountdown);

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(calculateTimeLeft() as ICountdown);
    }, 1000);

    return () => clearInterval(timer);
  }, [targetDate]);

  return (
    <Container>
      <Row>
        <Col>
          <div className="countdowTimer">
            <h1>{timeLeft.days || 0}D {timeLeft.hours || 0}H {timeLeft.minutes || 0}M {timeLeft.seconds || 0}S</h1>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export interface ICountdown {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}

export default CountdownTimer;
