import React, { useState } from "react";
import "./home.scss";
import CountdownTimer from "../../../components/countdowTimer/countdowTimer.tsx";
import FormTicket from "../../../components/form/formTicket.tsx";
import ImageCarousel from "../../../components/carousel/imageCarousel.tsx";
import AccordionComponent from "../../../components/accordion/accordionComponent.tsx";
import { Header } from "../../../components/header/header.tsx";
import { createTicket } from "./home.service.tsx";
import { generarPDF } from "../../../service/createPDF.js";
import { Loading } from "../../../components/loading/loading.tsx";
import { useNavigate } from "react-router-dom";

export const Home = () => {
  const navigate = useNavigate();
  const targetDate = "2025-07-19T23:59:59";
  const [showLoading, setShowLoading] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [menssageAlert, setMenssageAlert] = useState('');

  const setCreateTicket = (data: any) => {
    setShowLoading(true);
    setShowAlert(false)
    createTicket(data)
      .then((result) => {
        if (result !== false) {
          let isVip = result.vip === true ? "Si" : "No";
          let nameComplete = result.name + " " + result.surname1;
          generarPDF(nameComplete, result.phone.toString(), isVip);
          setShowLoading(false);
          navigate("/confirmed");
        } else {
          setMenssageAlert("Teléfono ya registrado")
          setShowLoading(false);
          setShowAlert(true)

          window.scrollTo({
            top: document.body.scrollHeight,
            behavior: 'smooth'
        });
        }
      })
      .catch((error) => {
        console.error("Error al crear ticket:", error);
        setShowLoading(false);
      });
  };
  return (
    <>
      <Loading loading={showLoading} />
      <div className="container">
        <div className="section1 d-flex justify-content-center align-items-center position-relative">
          <Header />
          <ImageCarousel />
          <CountdownTimer targetDate={targetDate} />
        </div>
        <div className="section2 ">
          <AccordionComponent />
        </div>

        <div className="section2 d-flex justify-content-center align-items-center position-relative">
          <FormTicket onSubmit={(e) => setCreateTicket(e)}  menssageAlert={menssageAlert} showAlert={showAlert} />
    
        </div>

      </div>
    </>
  );
};
