import React, { useEffect, useState } from "react";

import FormEditTicket from "../../../components/form/formEditTicket.tsx";
import { Loading } from "../../../components/loading/loading.tsx";
import { useNavigate } from "react-router-dom";
import {
  editTicketService,
  getTicket,
} from "../showTicket/showTicket.service.tsx";
import InputSearch from "../../../components/inputSearch/inputSearch.tsx";
import { generarPDF } from "../../../service/createPDF.js";

export const ShowTicketManual = () => {
  const navigate = useNavigate();
  const [qrResult, setQrResult] = useState(null as any);

  const [dataUser, setDataUser] = useState(null as any);
  const [showLoading, setShowLoading] = useState(false);
  useEffect(() => {
    const fetchData = async () => {
      setShowLoading(true);
      if (qrResult) {
        const result = await getTicket(qrResult);
        setDataUser(result);
        setShowLoading(false);
      } else {
        setShowLoading(false);
      }
    };

    fetchData();
  }, [qrResult]);

  const editTicket = (data: any) => {
    setShowLoading(true);
    editTicketService(data)
      .then((result) => {
        navigate("/privateHome");
        setShowLoading(false);
      })
      .catch((error) => {
        console.error("Error al editar ticket:", error);
        setShowLoading(false);
      });
  };

/**
 * Generamos PDF de nuevo
 * @param data Datos del usuario
 */
  const getPDF = (data: any) => {
    let isVip = data.vip === true ? "Si" : "No";
    let nameComplete = data.name + " " + data.surname1;
    generarPDF(nameComplete, data.phone.toString(), isVip);

  };

  return (
    <>
      <Loading loading={showLoading} />
      <div className="container">
        {dataUser && (
          <div className="section2 d-flex justify-content-center align-items-center position-relative">
            <FormEditTicket
              initialData={dataUser}
              onSubmit={(e) => editTicket(e)}
              onGetPDF={(e) => getPDF?.(e)}
            />
          </div>
        )}
        {!dataUser && (
          <div className="">
            <InputSearch
              onSubmit={(e) => setQrResult(e)}
            />
          </div>
        )}
      </div>
    </>
  );
};
