import React from "react";
import { Accordion } from "react-bootstrap";
import "./accordionComponent.scss";

const AccordionComponent = () => {
  return (
    <div className="bodyAccordion">
      <h3 className="title">Información</h3>
    <Accordion >
      <Accordion.Item eventKey="0">
        <Accordion.Header>¿Entradas?</Accordion.Header>
        <Accordion.Body>
          <strong>¿Entradas VIPS?</strong>
          <p>
            Para los 50 primeros que han reservado su entrada serán VIPS. Esto
            quiere decir que tendréis unas sorpresas durante la noche.
          </p>
          <strong>¿Qué pasa si ya no queda más entradas VIPS?</strong>
          <p>
            Puedes asistir igual, pero no serás VIP y tendrás que reservar de
            todas forma tu asistencia.
          </p>
          <strong>¿Puedo asistir sin entrada?</strong>
          <p>
            No, no puedes, ya que me he dedicado todo el santo año haciendo las
            aplicaciones y quiero darle uso XD
          </p>
          <strong>¿Y si pierdo mi entrada?</strong>
          <p>
            Espero que no, ya que logísticamente será difícil para mí, pero
            siempre podré consultar al sistema.
          </p>
        </Accordion.Body>
      </Accordion.Item>
      <Accordion.Item eventKey="1">
        <Accordion.Header>Acerca de</Accordion.Header>
        <Accordion.Body>
          <p>
            <strong>NO SE PERMITE PIROTÉCNICA</strong>
          </p>
          <p>Deberás de traer tu propio alcohol</p>
          <p>Respetar el medio ambiente</p>
          <p>No hay 'Dress Code'</p>
          <p>Podrás utiliza los parkings vips siempre que quede hueco</p>
          <p>
            Respecta a lo demás y serás respectado, cualquier causa de conflicto
            da igual la parte que sea serás <strong>EXPULSADO</strong>
          </p>
          <p>
            El evento finaliza cuando nos quedemos sin alpiste y hasta que el
            cuerpo aguante
          </p>
        </Accordion.Body>
      </Accordion.Item>
    </Accordion>
    </div>
  );
};

export default AccordionComponent;
