import QRCode from "qrcode";

export function generarPDF(nombre, telefono, vip) {
  const { jsPDF } = window.jspdf;
  const doc = new jsPDF({ orientation: "portrait", unit: "mm", format: "A5" });

  // Ruta de la imagen de fondo
  const bannerImgPath = require("./banners/diseño_PDF.png");

  // Generar el QR como base64 directamente (sin usar el DOM)
  QRCode.toDataURL("THERAVE" + telefono, { width: 100,    height: 100 }, function (err, qrDataUrl) {
    if (err) {
      console.error("Error generando QR:", err);
      return;
    }

    // Cargar imagen de fondo
    loadImageToBase64(bannerImgPath, function (bannerImg) {
      doc.addImage(bannerImg, "JPEG", 0, 0, 150, 210);

      // Texto con info del asistente
      doc.setFontSize(10);
      doc.setFont("helvetica", "bold");
      doc.text("Fecha del evento:", 35, 43);
      doc.setFont("helvetica", "normal");
      doc.text(
        "19 de julio de 2025 a partir de 23:30",
        40 + doc.getTextWidth("Fecha del evento:"),
        43
      );

      doc.setFont("helvetica", "bold");
      doc.text("Nombre y Apellidos:", 35, 51);
      doc.setFont("helvetica", "normal");
      doc.text(nombre, 40 + doc.getTextWidth("Nombre y Apellidos:"), 51);

      doc.setFont("helvetica", "bold");
      doc.text("Teléfono:", 35, 59);
      doc.setFont("helvetica", "normal");
      doc.text(telefono, 40 + doc.getTextWidth("Teléfono:"), 59);

      doc.setFont("helvetica", "bold");
      doc.text("Vip:", 35, 67);
      doc.setFont("helvetica", "normal");
      doc.text(vip, 36 + doc.getTextWidth("Teléfono:"), 67);

      // QR en el PDF
      doc.addImage(qrDataUrl, "JPEG", 65, 90, 50, 50);

      // Código único
      doc.setFontSize(10);
      doc.setFont("helvetica", "normal");
      doc.setTextColor(0, 150, 136);
      doc.text("THERAVE" + telefono, 72, 145);

      // Condiciones
      doc.setFontSize(8);
      doc.setTextColor(33, 33, 33);
      const condiciones = [
        "EVENTO PARA MAYORES DE 15 AÑOS. Permite el acceso al recinto.",
        "Menores de 15, 16 y 17 años solo con aprobación por Padre/Madre/Tutor",
        "al responsable del evento.",
      ];
      let yPos = 170;
      condiciones.forEach((line) => {
        const textWidth = doc.getTextWidth(line);
        const xPos = Math.max((210 - textWidth) / 87, 87);
        doc.text(line, xPos, yPos, { maxWidth: 190, align: "center" });
        yPos += 8;
      });

      // Guardar PDF
      doc.save(`Entrada_${nombre.replace(/\s+/g, "_")}.pdf`);
    });
  });

  function loadImageToBase64(url, callback) {
    const img = new Image();
    img.crossOrigin = "Anonymous";
    img.src = url;
    img.onload = function () {
      const canvas = document.createElement("canvas");
      canvas.width = img.width;
      canvas.height = img.height;
      const ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0);
      callback(canvas.toDataURL("image/png"));
    };
    img.onerror = function () {
      console.error("Error al cargar la imagen:", url);
    };
  }
}
